.wrapper {
  display: flex;
  background-color: #282c34;
  min-height: 100vh;
  box-sizing: border-box;
  flex-wrap: wrap;
}

.settingsBlock {
  display: flex;
  flex: 1 0 0;
  justify-content: center;
  padding: 30px;
  border: aquamarine 3px solid;
  align-items: center;
  flex-direction: column;
}

.counterBlock {
  display: flex;
  flex: 1 0 0;
  justify-content: center;
  padding: 30px;
  border: aquamarine 3px solid;
  align-items: center;
  flex-direction: column;
}