.plus {
    background-color: aquamarine;
    padding: 10px;
    border-radius: 10px;
    cursor: pointer;
    font-size: 16px;
}
.plus:hover {
    background-color: mediumaquamarine;
}

.reset {
    background-color: aquamarine;
    padding: 10px;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
}
.reset:hover {
    background-color: mediumaquamarine;
}