.counterWrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    color: aquamarine;
    padding: 50px 20px;
    border: aquamarine 5px solid;
    min-width: 70%;
}

.counterStyle {
    font-size: 24px;
    margin-bottom: 30px;
}

.error {
    color: red;
    font-size: 24px;
    margin-bottom: 30px;
}

.buttonBlock {
    justify-content: space-around;
    display: flex;
    min-width: 100%;
}
